<template>
  <div>
    <!-- <span v-touch:tap="touch"
      v-touch:longtap="touch"
      v-touch:swipe.left="touch"
      v-touch:swipe.right="touch">Mix Multiple Events
      <hr></span> -->
    <input type="hidden" @keyup="keyPressed" />
    <v-card
      v-if="images.length != 0" 
      class="slides"
      @mousedown="mouseDown"
      @mouseup="mouseUp"
      @click="click"
      color="black">
      <v-img
        class="slide"
        aspect-ratio="16.9"
        height="100%"
        contain
        lazy-src
        :images="images"
        :src="`/${images[currentIndex]}`"/>
    </v-card>
    <div v-else/>
  </div>
</template>


<script>
import axios from 'axios'
export default {
  components: {},
  data: () => {
    return {
      images: [],
      interval: 7000,
      index: 0,
      mouseDownX: 0,
      mouseUpX: 0,
      start: null
    };
  },
  computed: {
    currentIndex() {
      return this.index
    },
  },
  methods: {
    touch (event) {
      console.log('touch', event)
    },
    click(event){
      // console.log('click count', event.detail)
      if (event.detail === 2){
        this.doubleClick()
      }
      if (event.detail === 3) {
        this.tripleClick()
      }
      if (event.detail === 1) {
        this.slide()
        // console.log('single click........')
      }
    },
    slide(){
      if (this.start === null) {
        console.log('starting slide')
        this.start = setInterval(() => {
          this.changeFwd()
          console.log("set interval...", this.start)
        }, this.interval);        
      }
      else {
        console.log('pausing slide')
        clearInterval(this.start)
        this.start = null
      }

    },
    mouseDown(event){
      // console.log('mouse down..............')
      this.mouseDownX = event.clientX
      console.log("calculated..", this.mouseDownX)
    },
    mouseUp(event){
      // console.log('mouse up..............')
      this.mouseUpX = event.clientX
      console.log("computed mouse up....", this.mouseUpX)
      if (this.mouseDownX + 200 < event.clientX) {
        console.log('you swiped right')
        this.slide()
        this.changeFwd()
        this.slide()
      }
      if (this.mouseDownX - 200 > event.clientX ) {
        console.log('you swiped left')
        this.slide()
        this.changeBwd()
        this.slide()
      }
    },
    doubleClick(){
      console.log('double clicked.........')
      this.changeFwd()
    },    
    tripleClick(){
      console.log('triple clicked............')
      // what the hell, triple click reload page
      location.reload()    
    },
    keyPressed(){
      // reference mounted event listener
    },
    changeFwd(){
      if (this.index === this.images.length - 2) {
        console.log("get more images....")
        this.getImages()
      }

      (this.index + 1 === this.images.length) 
      ? (this.index = 0)
      : (this.index = this.index + 1)
      console.log("changeFwd index", this.index)
    },
    changeBwd(){
      (this.index === 0)
      ? (this.index = this.images.length - 1)
      : this.index--
      console.log("changeBwd index", this.index)
    },
    // getImages(){
    //   return this.$store.state.images;
    // },
    getImages(){
      console.log("mounted, getting images...")
      this.images = []
      axios.get("/api/images")
        .then(images => {
          console.log(images.data.images)
          images.data.images.forEach(image => {
            this.images.push(image)
          });
          return images.data.images.length
        })
        .then(res => {
           if (res && !this.start) {
              // if image length not 0, and not already started.. start slide
              this.slide()
           }
        })
        .catch(err => {console.log('could not get images - error..', err)})
    }
  },
  mounted(){
    // this.images = this.$store.state.images
    this.getImages()
    var self = this
    window.addEventListener('keyup', function(event) {
      switch (event.keyCode)
      { 
        // right=39 or up=38 changeFwd
        case 38:
        case 39:
          console.log('move forward..')
          // we pause it first to reset the set interval
          self.slide()
          self.changeFwd()
          self.slide()  // then restart
          break
        // left=37 or down=40 changeBwd 
        case 37:
        case 40:
          console.log('move back..')
          self.slide()
          self.changeBwd()
          self.slide()
          break
        case 32: // enter/space pressed pause/unpuase
        case 13:  
          self.slide()
          break  
        default:
          console.log("key pressed.. ", event.keyCode)
      }
    })

    // start the slide
    // console.log("mounted, starting slide...")
    // this.slide()
  }
}
</script>

<style>
*,
html,
body{
  overflow: hidden;
}
pre{
    background-color: white;
    color: black;
    font-size: 80%;
}
.slides{
  height:100vh;
  border: 4px;
  color: white;
  border-color: white;
  padding: 4px;
}
</style>

